// Fonts and Font Mixins
// --------------------------------------------------

// Fix font smoothing. This makes Mac OS look way better.
// http://www.intridea.com/blog/2014/5/8/better-font-smoothing-in-chrome-on-mac-os-x
html {
    -webkit-font-smoothing: antialiased;
}

@media
only screen and (-webkit-min-device-pixel-ratio: 1.25),
only screen and ( min-device-pixel-ratio: 1.25),
only screen and ( min-resolution: 200dpi),
only screen and ( min-resolution: 1.25dppx) {
    html {
        -webkit-font-smoothing: subpixel-antialiased;
    }
}

// Full antialiasing can be too difficult to read in
// some cases such as light text on dark backgrounds.
// Use this mixin to restore the aliasing to default.
.defaultAntialiasing() {
    -webkit-font-smoothing: subpixel-antialiased;
}

// Font Ramp
// --------------------------------------------------
.font-size-tiny() {
    font-size: 9px;
}

.font-size-xs() {
    font-size: 11px;
}

.font-size-small() {
    font-size: 13px;
}

.font-size-medium() {
    font-size: 16px;
}

.font-size-large() {
    font-size: 20px;
}

.font-size-xl() {
    font-size: 24px;
}

.font-size-xxl() {
    font-size: 30px;
}

.font-size-xxxl() {
    font-size: 48px;
}

// Segoe UI (styles taken from Fabric)
// variable @fontSource is set by webpack
// --------------------------------------------------
@font-face {
    font-family: "Segoe UI Light";
    src:
        local("Segoe UI Light"),
        url('@{fontSource}/fonts/SegoeUI-Light.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "Segoe UI Semilight";
    src:
        local("Segoe UI Semilight"),
        url('@{fontSource}/fonts/SegoeUI-Semilight.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "Segoe UI Regular";
    src:
        local("Segoe UI"),
        url('@{fontSource}/fonts/SegoeUI-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "Segoe UI Semibold";
    src:
        local("Segoe UI Semibold"),
        url('@{fontSource}/fonts/SegoeUI-Semibold.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "Segoe UI Bold";
    src:
        local("Segoe UI Bold"),
        url('@{fontSource}/fonts/SegoeUI-Bold.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

// San Francisco
// --------------------------------------------------
@font-face {
	font-family: 'SF Light';
	font-style: normal;
    font-weight: normal;
	src: local(".SFNSDisplay-Ultralight"), local(".HelveticaNeueDeskInterface-Thin");
}

@font-face {
    font-family: "SF Semilight";
    font-weight: normal;
    font-style: normal;
    src: local(".SFNSDisplay-Light"), local(".HelveticaNeueDeskInterface-Light");
}

@font-face {
	font-family: 'SF Regular';
	font-style: normal;
    font-weight: normal;
	src: local(".SFNSDisplay-Regular"), local(".HelveticaNeueDeskInterface-Regular");
}

@font-face {
	font-family: 'SF Semibold';
	font-style: normal;
    font-weight: normal;
	src: local(".SFNSDisplay-Bold"), local(".HelveticaNeueDeskInterface-Bold");
}

@font-face {
	font-family: 'SF Bold';
	font-style: normal;
    font-weight: normal;
	src: local(".SFNSDisplay-Heavy"), local(".HelveticaNeueDeskInterface-Heavy");
}

.system-stack() {
    font-family: 'SF Regular', 'Segoe UI Regular', sans-serif;
}

// Skype Assets
// --------------------------------------------------

@font-face {
    font-family: 'SkypeAssets-Light';
    src: url('../resources/fonts/SkypeAssets-Light.woff2') format('woff2');
    font-style: normal;
    font-weight: normal;
    unicode-range: U+E001-FFFF;
}

.icon-size(@size: 10px) {
    height: @size;
    width: @size;
    font-size: @size;
    line-height: 1;
}
