@fontSource: '../resources';
/*!
* Skype Base Web Styles
*/

// Normalize.CSS reset sheet
// --------------------------------------------------
@import (inline) "../../node_modules/normalize.css/normalize.css";

// Global Componenets
// --------------------------------------------------
@import "typography.less";

// Mixins
.placeholder(@color) {
    &::-webkit-input-placeholder {
        color: @color;
    }
    &:-moz-placeholder {
        color: @color;
    }
    &::-moz-placeholder {
        color: @color;
    }
    &:-ms-input-placeholder {
        color: @color;
    }
}

// Override External Styles
// --------------------------------------------------
@import "overrides.less";

// Styling for elements created by the calling modules
.webVideoContainer video {
    width: 100%;
    height: 100%;
}

.webVideoContainer canvas {
    width: 100%;
    height: 100%;
}

.webVideoContainer embed {
    width: 100%;
    height: 100%;
}

// Styling animations for SpriteComponent
@keyframes SpriteComponentAnimation {
    0% { transform: translate3d(0, 0, 0) }
}

.bodySpritePauser .SpriteComponentAnimationPause {
    animation-play-state: paused !important;
}

// Styling used by formatted messages sent by Teams
.copy-paste-table {
    border: solid 1px rgba(22,35,58,0.36);
    background-color: #fff;
    color: #252423;
    border-collapse: collapse
}
.copy-paste-table td,.copy-paste-table th {
    padding: 4pt;
    border: solid 1px rgba(22,35,58,0.36)
}

.largeSpinner {
    height: 3em;
    width: 3em;
    margin-top: 40px;
  }

  .largeSpinner .spinner {
    height: 100%;
    position: relative;
    width: 100%
  }

  .largeSpinner .spinner .circle {
    height: 16%;
    left: 42%;
    top: 42%;
    width: 16%;
    position: absolute
  }

  .largeSpinner .spinner .circle div {
    height: 100%;
    position: absolute;
    width: 100%
  }

  .largeSpinner .spinner .circle .position {
    top: -250%
  }

  .largeSpinner .spinner .circle .shape {
    background-color: #0078CA;
    border-radius: 50%;
    overflow: hidden
  }

  .largeSpinner .spinner.small .circle {
    height: 24%;
    left: 38%;
    top: 38%;
    width: 24%
  }

  .largeSpinner .spinner.small .circle .position {
    top: -150%
  }

  .largeSpinner .spinner.blue .circle .shape {
    background-color: #0078CA;
  }

  .largeSpinner .spinner.blue .circle .shape:after {
    border-color: #0078CA;
  }

  .largeSpinner .spinner.white .circle .shape {
    background-color: #fff
  }

  .largeSpinner .spinner.white .circle .shape:after {
    border-color: #fff
  }

  .largeSpinner .spinner .circle .rotate, .largeSpinner .spinner .circle .scale {
    -webkit-animation-direction: normal;
    animation-direction: normal;
    -webkit-animation-duration: 1.4s;
    animation-duration: 1.4s;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear
  }

  .largeSpinner .spinner .circle.one .rotate {
    -webkit-animation-name: spinner-one-rotate;
    animation-name: spinner-one-rotate
  }

  .largeSpinner .spinner .circle.one .scale {
    -webkit-animation-name: spinner-one-scale;
    animation-name: spinner-one-scale
  }

  .largeSpinner .spinner .circle.two .rotate {
    -webkit-animation-name: spinner-two-rotate;
    animation-name: spinner-two-rotate
  }

  .largeSpinner .spinner .circle.two .scale {
    -webkit-animation-name: spinner-two-scale;
    animation-name: spinner-two-scale
  }

  .largeSpinner .spinner .circle.three .rotate {
    -webkit-animation-name: spinner-three-rotate;
    animation-name: spinner-three-rotate
  }

  .largeSpinner .spinner .circle.three .scale {
    -webkit-animation-name: spinner-three-scale;
    animation-name: spinner-three-scale
  }

  .largeSpinner .spinner .circle.four .rotate {
    -webkit-animation-name: spinner-four-rotate;
    animation-name: spinner-four-rotate
  }

  .largeSpinner .spinner .circle.four .scale {
    -webkit-animation-name: spinner-four-scale;
    animation-name: spinner-four-scale
  }

  .largeSpinner .spinner.small .circle.one .rotate {
    -webkit-animation-name: spinner-small-one-rotate;
    animation-name: spinner-small-one-rotate
  }

  .largeSpinner .spinner.small .circle.one .scale {
    -webkit-animation-name: spinner-small-one-scale;
    animation-name: spinner-small-one-scale
  }

  .largeSpinner .spinner.small .circle.two .rotate {
    -webkit-animation-name: spinner-small-two-rotate;
    animation-name: spinner-small-two-rotate
  }

  .largeSpinner .spinner.small .circle.two .scale {
    -webkit-animation-name: spinner-small-two-scale;
    animation-name: spinner-small-two-scale
  }

  .largeSpinner .spinner.small .circle.three .rotate {
    -webkit-animation-name: spinner-small-three-rotate;
    animation-name: spinner-small-three-rotate
  }

  .largeSpinner .spinner.small .circle.three .scale {
