  // --------------------------------------------------
  // lib\adaptivecards - We can only change styles of adaptivecards lib by directly writing styles here.

  // This is needed to add flex-flow wrap to the action set of new adaptive cards library.
  .ac-actionSet {
    gap: 8px;
    flex-flow: wrap;
  }

  // At the moment, there's no other way to override the default styles of the adaptive cards control popup.
  // Therefor it is overridden like this and theme neutral colors are used.
  .ac-ctrl-overlay {
    position: absolute;
  }

  button.ac-pushButton {
    background-color: transparent;
    border: none;
    padding: 0 15px;
    color: #777;
  }

  button.ac-pushButton:hover {
    background-color: rgba(100, 100, 100, 0.1);
  }

  .ac-ctrl-popup-container {
    border: 1px solid #333333;
    background-color: #333333;
    color: #FFFFFF;
    position: absolute;
    box-shadow: 0 0 15px -5px rgba(0, 0, 0, 0.4);
    overflow-y: auto;
  }

  .ac-ctrl-dropdown-item {
    width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    display: block;
    padding: 0px 12px 0px 12px;
    line-height: 30px;
    cursor: pointer;
  }

  .ac-ctrl-dropdown-item:hover {
    background-color: rgba(0, 0, 0, 0.1);
  }

  // lib\adaptivecards - end section
  // --------------------------------------------------