// Top Level Page Elements
// --------------------------------------------------
html {
    height: 100%;

    font-size: 16px;
    .system-stack();

    // Required to prevent the gray highlight when tapping on targets in phonegap
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-tap-highlight-color: transparent; /* For some Androids */

    // Required to support long press on Safari/iOS
    -webkit-touch-callout: none;
}

body,
.app-container {
    height: 100%;
    width: 100%;
    display: flex;
    overflow-y: hidden;
    user-select: none;
    -webkit-user-select: none;

    -webkit-overflow-scrolling: touch;
}

// Override platform-specific styles on textarea.
textarea {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    .placeholder(#bbb);
}

input {
    .placeholder(#bbb);
}

// Override placeholder color for input with gradient background
.inputGradient * {
    .placeholder(rgba(255,255,255,0.3));
}

// SVGs should be block by default so the honor width
// and height settings.
svg {
    display: block;
}

// Override browser default for box-sizing.
*,
*:before,
*:after {
    box-sizing: border-box;
}

// Override browser default for focused element
.noFocusOutline {
    *:focus {
        outline: 0;
    }
}

// Hide the "clear" button in text inputs on IE.
input::-ms-clear {
    display: none;
}

// By default all links are without decoration. If any decoration is needed it
// needs to be applied manually at platform specific View/Text component
a {
    text-decoration: none;
}

video {
    width: 100%;
}

button {
    padding: 0;
}

@media (prefers-color-scheme: dark) {
    body,
    .app-container {
        background-color: #19191B
    }
}

// Overrite browser default for input autofill
.disableAutoFill {
    input {
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: inherit
    }
}